// (Detail.js파일임)

import axios from 'axios';
import React, {useEffect, useState} from 'react'
import { useHistory, useParams } from 'react-router-dom';
import styled from 'styled-components'

import './Detail.scss';

let Box = styled.div`
    padding: 20px;
    border: solid 1px grey;
`;



let Title = styled.h4`
    font-size: 25px;
    color : ${props => props.colors}
`   // 색갈만 다른 title이 여러개 필요하면?

let alertPop = document.querySelector('.myAlert-pink');



function Detail(props) {    


    let [alert, alertChange] = useState(true)


    useEffect(()=>{

        axios.get();
        // 2초후에 alert창 사라지게 함
        let timer = setTimeout(()=>{
            let alertPop = document.querySelector('.myAlert-pink');
            alertPop.style.display = 'none'
        return ()=>{clearTimeout(timer)} //이렇게 해야 완전히 사라진다.
        },2000,[])
        // 얘는 컴포넌트 등장/업데이트시 매번 실행된다.
        // 끝에 []를 넣어 시작조건을 넣을 수 있다
        // []가 아무것도 안넣으면, <Detail>등장시 한 번만 실행되고 끝남
    });



    let history = useHistory();
    let { id } = useParams(); //파라미터값 저장하는 useParams;

    let findId = props.shoes.find(function(a) {
        return a.id == id;
    })


    {console.log(props.shoes[id].id+1);}
//    / {console.log('https://codingapple1.github.io/shop/shoes'+(id)+'.jpg')};
    return (

      <div className="container">
          <Box>dfdfdf
              <Title className="red">hello</Title>
          </Box>

          {
              alert === true 
              ? (   <div className="myAlert-pink">
                    <p>재고가 얼마 남지 않았습니다.</p>
                    </div>)
              : null
          }

          <input/>

            <div className="row">
                <div className="col-md-6">
                <img width = "100%" src={'https://codingapple1.github.io/shop/shoes'+(props.shoes[id].id+1)+'.jpg'} />
                </div>
                <div className="col-md-6 mt-4">
                   <h4 className="pt-5">{findId.title}</h4>
                   <p>{findId.content}</p>
                   <p>{findId.price}원</p>

                   <Stock stock={props.stock}></Stock>
                  <button className="btn btn-danger"
                  onClick={()=>{
                      let stockCopy=[...props.stock]; 
                      stockCopy[1]>0 ? stockCopy[1]--: console.log('ff'); props.stockChange(stockCopy) }}>주문하기</button> 
                  <button className="btn btn-danger" onClick = {()=>{history.goBack(
                    //   histroy.특정경로로 이동시키려면? history.push('/blablabla')
                  )}}>뒤로가기</button> 
                </div>
            </div>
        </div> 
    )
  }

  function Stock(props) {
      return (
      <p> 재고 : {props.stock[1]} </p>
      )
  }

  export default Detail;